<template>
  <div class="wrapper" @click.stop="close">
    <div class="centerWrapper">
      <div id="content" @click.stop="showLuziDetail">
        <div class="content-wrapper">
          <div class="left-tj">
            <div class="left-content">
              <span></span>
              <p>{{totalData.all}}</p>
            </div>
            <div class="left-content">
              <span></span>
              <p>{{totalData.big}}</p>
            </div>
            <div class="left-content">
              <span></span>
              <p>{{totalData.small}}</p>
            </div>
          </div>
          <div class="right-num">
            <ul class="right-content">
              <li class="lie" v-for="(itemArray,index) in dataArray" :key="index">
                <div class="row" v-for="(item,index2) in itemArray" :key="index2">
                  <p>{{item.result}}</p>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    luziData: {
      type: Array
      // required: true
    },
    showHxjcLuziDetail: {
      type: Function
    }
  },
  watch: {
    luziData: function(newArray, oldArray) {
      this.dataArray = this.getArrayByLength(this.luziData, 6);
    }
  },
  data() {
    return {
      animationTime: 100, // 
      animationShowing: false, //
      totalData: { all: 0, big: 0, small: 0 },
      data: [], //
      dataArray: [] //6
    };
  },
  mounted() {
    this.showAnimatioin();
    this.dataArray = this.getArrayByLength(this.luziData, 6);
  },
  components: {},
  methods: {
    close() {
      this.closeAnimatioin();
    },
    //
    showLuziDetail() {
      this.showHxjcLuziDetail();
    },
    // 
    showAnimatioin() {
      if (this.animationShowing) {
        return;
      }
      let timer = null;
      let num = 0;
      this.animationShowing = true;
      timer = setInterval(() => {
        num++;
        let progressImg = document.getElementById("content");
        progressImg.style.left = num - 100 + "%";
        if (num == this.animationTime) {
          clearInterval(timer);
          this.animationShowing = false;
        }
      }, 5);
    },
    //
    closeAnimatioin() {
      if (this.animationShowing) {
        return;
      }
      let timer = null;
      let num = this.animationTime;
      this.animationShowing = true;
      timer = setInterval(() => {
        num--;
        let progressImg = document.getElementById("content");
        progressImg.style.left = num - 100 + "%";
        if (num == 0) {
          clearInterval(timer);
          this.animationShowing = false;
          this.$emit("close");
        }
      }, 5);
    },
    //
    getArrayByLength(array, size) {
      //
      this.totalData = { all: 0, big: 0, small: 0 };
      array.forEach(item => {
        console.log(item.result);
        this.totalData.all += 1;
        if (item.result >= 5) {
          this.totalData.big += 1;
        } else {
          this.totalData.small += 1;
        }
      });
      //
      var count = 90 - array.length;
      if (count > 0) {
        for (var i = 0; i < count; i++) array = [...array, { result: "" }];
      }
      //undefined
      const length = array.length;
      //sizesize1
      if (!length || !size || size < 1) {
        return [];
      }
      //
      let index = 0; //start
      let resIndex = 0; //

      //lengthsize
      let result = new Array(Math.ceil(length / size));
      //
      while (index < length) {
        //result[0]result[1]array.slice
        result[resIndex++] = array.slice(index, (index += size));
      }
      //
      return result;
    },
  }
};
</script>

